/*
 * 1. Google Font
 */

@import url(https://fonts.googleapis.com/css?family=Work+Sans:300,400,600,700);

/*
 * 2.0 Global Style
 */

html {
    font-family: 'Work Sans', sans-serif;
    font-size: 16px;
}

body {
    color: #4e4e4e;
    font-family: 'Work Sans', sans-serif;
    font-size: 16px;
    font-weight: 300;
    line-height: 1.6;
    position: relative;
    -webkit-font-smoothing: antialiased;
}

a {
    color: #DD0155;
    &:focus {
        outline: none;
    }
    &:hover {
        color: #FF0055;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 600;
}

p {
    margin: 0 0 30px;
}

ul {
    li {
        b {
            font-weight: 400;
        }
        margin-bottom: 10px;
    }
}

.card {
    h2 {
        margin-top: 0px;
        font-size: 25px;
        font-weight: 600;
        margin-bottom: 2px;
    }
    svg {
        margin-bottom: 6px;
    }
    display: inline-block;
    width: 182px;
    font-weight: normal;
}

.custom-list-icon {
    list-style-type: none;
    padding: 0;
    li {
        position: relative;
        padding-left: 20px;
        &::before {
            position: absolute;
            left: 0;
            color: #4e4e4e;
            font-family: "Ionicons";
        }
    }
}

.social-block {
    list-style-type: none;
    padding: 0;
    li {
        display: inline-block;
        a {
            display: inline-block;
            height: 32px;
            width: 32px;
            color: #4e4e4e;
            i {
                font-size: 1.5em;
            }
            &:hover,
            &:focus {
                color: #000;
            }
        }
    }
}

.btn.active.focus,
.btn.active:focus,
.btn.focus,
.btn:active.focus,
.btn:active:focus,
.btn:focus {
    outline: none;
}

.btn {
    border: 0;
    border-radius: 0;
    padding: 6px 28px;
    text-transform: uppercase;
    transform: translateZ(0);
    backface-visibility: hidden;
    -moz-osx-font-smoothing: grayscale;
    transition-duration: 0.3s;
    transition-property: transform;
    &:hover {
        transform: scale(0.9);
    }
}

.form-control {
    border-radius: 0;
    box-shadow: none;
    height: 48px;
    padding: 10px 12px;
    font-size: 16px;
    line-height: 1.6;
    
}

.valign-center {
    display: flex;
    align-items: center;
}

.section {
    padding: 70px 0;
}

.section-title {
    color: #313131; // letter-spacing: 1px;
    margin: 0 0 20px; // text-transform: uppercase;
    text-align: center;
}


/*
 * 3.0 Helper Class
 */


/*
 * 3.1 Helper Class: font-weight
 */

.font-300 {
    font-weight: 300;
}

.font-400 {
    font-weight: 400;
}

.font-700 {
    font-weight: 700;
}


/*
 * 3.2 Helper Class: margin
 */

.mt0 {
    margin-top: 0;
}

.mb0 {
    margin-bottom: 0;
}

.mt20 {
    margin-top: 20px;
}


/*
 * 3.3 Helper Class: list
 */

.list-arrow-right {
    @extend .custom-list-icon;
    li {
        &::before {
            content: "\f3d3";
        }
    }
}


/*
 * 3.4 Helper Class: button
 */

.btn-white {
    color: #000;
    background-color: #fff;
    &:hover,
    &:focus {
        color: #000;
        background-color: #fff;
    }
}

.btn-black {
    color: #fff;
    background-color: #000;
    &:hover,
    &:focus {
        color: #fff;
        background-color: #000;
    }
}


/*
 * 3.5 Helper Class: background
 */

.bg-image {
    position: relative;
    &::before {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }
}

.beach1 {
    @extend .bg-image;
    background: url(../images/backgrounds/beach1.jpg) no-repeat center center / cover;
}

.beach2 {
    @extend .bg-image;
    background: url(../images/backgrounds/beach2.jpg) no-repeat center bottom / cover;
}

.aerial {
    @extend .bg-image;
    background: url(../images/backgrounds/aerial.jpg) no-repeat center bottom / cover;
}

@media (min-width:768px) {
    .section {
        padding: 90px 0;
    }
    .section-title {
        color: #313131; // letter-spacing: 1px;
        margin: 0 0 60px; // text-transform: uppercase;
        text-align: left;
    }
}